import { Link } from "gatsby";
import React from "react";
import PropTypes from "prop-types";

const featuredProject = ({ name, type, technologies, description, color }) => (
  <section
    className={`w-10/12 sm:w-7/12 md:w-2/3 lg:w-2/5 max-w-xs mb-5 bg-${color}-300 rounded shadow cursor-pointer  featured`}
  >
    <header className={`bg-${color}-200 text-${color}-700 p-3`}>{name}</header>
    <main style={{ hyphens: "auto" }} className="p-3 ">
      {description}
    </main>
    <footer
      className={`flex flex-row justify-between p-3 text-${color}-800 text-sm`}
    >
      <span className="w-3/5">
        {technologies.join(", ")}
      </span>
      <span>{type}</span>
    </footer>
  </section>
);

featuredProject.propTypes = {
  name: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  technologies: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default featuredProject;
